// stylelint-disable at-rule-empty-line-before

@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";

// Information Block

.infromation-blank {
	background-color: #fff;
	max-width: 660px;
	margin: 60px auto;
}
.person {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	padding: 27px 20px 32px 20px;
	border-bottom: 2px solid $color-section-border-bottom;

	&__avatar {
		padding: 0 20px 22px 0;
		flex-shrink: 0;

		&--circle {
			width: 138px;
			height: 138px;
			overflow: hidden;
			border-radius: 50%;
		}
	}

	&__information {
		width: 100%;

		&__text-group {
			position: relative;
			padding: 0 0 31px 0;
			margin: 0;
			z-index: 1;

			&:last-child {
				padding: 0;
			}
			
			input {
				border: 0;
				border-bottom: 2px solid $color-input-border;
				background-color: transparent;
				width: 100%;
				padding: 0 8px 7px 0;
				text-transform: uppercase;

				&:focus,
				&.is-have-inform {

					~ label {
					    font-size: 10px;
					    color: #adadad;
					    top: -18px;
					}

				}
			}

			label {
			    position: absolute;
			    top: 0;
			    left: 0;
			    text-align: left;
			    text-transform: uppercase;
			    margin: 0;
			    padding: 0;
			    transition: all .12s;
			    z-index: -1;
			}

		}

	}
}

.skills {
	padding: 32px 20px 38px 20px;
	border: 0;
	border-bottom: 2px solid $color-section-border-bottom;

	h4 {
		font-size: 18px;
		color: $color-important-text;
		margin: 0;
		padding: 0;
	}

	&__container {
		padding: 22px 0 0 0;
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
	}

	&__section {
	    width: 160px;
	    max-width: 25%;
	    padding: 0 10px 0 0;
	    flex-shrink: 0;
	}

}

.checkbox {
	margin: 0 0 42px 0;
	position: relative;

	&:last-child {
		margin: 0;
	}

	input {
		display: none;

		&:checked {

			~label {

				&:before {
					opacity: 1;
					transform: rotate(-45deg) scale(1);
				}
				
				&:after {
					background-color: $color-checked-background;
				}

			}

		}

	}

	label {
		position: relative;
		padding: 0 0 0 27px;
		text-transform: uppercase;
		user-select: none;
		cursor: pointer;

		&:before {
	        content: '';
		    width: 12px;
		    height: 6px;
		    position: absolute;
		    top: 2px;
		    left: 4px;
		    border: 2px solid white;
		    border-top: none;
		    border-right: none;
		    background: transparent;
		    opacity: 0;
		    transform: rotate(-45deg) scale(0);
		    z-index: 1;
		    transition: all 0.3s;
		}

		&:after {
			content: '';
			width: 19px;
			height: 19px;
			border-radius: 5px;
			background-color: $color-no-checked-background;
			position: absolute;
			left: 0;
			top: -3px;
			transition: all 0.3s ease-in;
		}
	}
}

.js-skills {
	padding: 20px 20px 30px 20px;
	display: flex;
	justify-content: flex-start;
	align-items: center;

	.js-skills__meter {
		flex-shrink: 0;
		
		svg.meter {
			display: block;
			width: 253px;
			height: 200px;

			g.meter-arrow {
				transform: translate(130px, 160px);
			}

			.meter-arrow-circle, #progress {
				fill: #3e3d40;
			}

			.meter-indicator-amber {
				transform: skewX(0deg) skewY(2deg);
				stroke-width: 48px;
				stroke-dashoffset: 343;
				stroke-dasharray: 94 547;
				stroke: #ffc815;
				fill: none;
			}

			.meter-indicator-green {
				stroke-width: 48px;
				stroke-dashoffset: 117;
				stroke-dasharray: 113 400;
				stroke: #a3cd3b;
				fill: none;
			}

			.meter-indicator-blue {
				transform: skewX(0deg) skewY(1deg);
				stroke-width: 48px;
				stroke-dashoffset: 124;
				stroke-dasharray: 100 536;
				stroke: #0093d7;
				fill: none;
			}

		}

	}

	.js-skills__rang-text {
		padding: 6px 30px 0 67px;

		.js-skills__rang-title {
			font-size: 18px;
			color: $color-important-text;
		}

		.js-skills__rang-on-number {
			font-family: $font-family-robotob;
			font-size: 50px;
			color: #0093d7;
			padding: 2px 0 0 0;
		}

	}

}

.rang_number {
  font: 400 13.3333px Arial;
  width: 100%;
  margin-top: 40px;
  text-align: center;
  border-width: 0;
  font-size: 50px;
  font-weight: 900;
  background-color: #faf9f7;
  color: #0093d7;
}
